import React, { Component } from 'react';
import { Form, Text, Scope, TextArea } from 'informed';
import '../styles/blog.css'

export class BlogEditor extends  Component {
	constructor(props) {
		super(props);
	}

	render() {
		return (
			<Form id="intro-form">
				<Text field="name" id="title" placeholder="Title" />
				<TextArea field="newEntry" id="content" />
				<button type="submit">
					Submit
				</button>
			</Form>
		)

	}
}
export default BlogEditor


