import React, { Component } from 'react';
import '../styles/blog.css'

export class NewBlog extends  Component {
	constructor(props) {
		super(props);
		this.pickLocation = this.pickLocation.bind(this);
		this.openBlogWindow = this.openBlogWindow.bind(this)
	}
	pickLocation = () => {
		this.props.pickLocation()
	};
	openBlogWindow = () => {
		console.log(this.props)
		this.props.closeModal();
		this.props.openBlogWindow()
	}

	render() {
		return (
				<ul className="location-options-list">
					<li><a href="#" onClick={this.openBlogWindow}>Use My Current Location</a></li>
					<li><a href="#" onClick={this.pickLocation}>Pick A Custom Location</a></li>
					<li><a href="#" onClick={this.openBlogWindow}>No Location</a></li>
				</ul>
		)

	}
}
export default NewBlog


